.container {
  max-width: 1490px;
  margin: 0 auto;
}

.search-form {
  height: 10vh;
  background-color: #add8e6;
  justify-content: center;
  align-items: center;
  display: flex;
}

.form__input {
  width: 250px;
  height: 25px;
  border-style: none;
  border-radius: 10px;
  padding-left: 10px;
  padding-right: 10px;
}

.form__input:focus {
  border-color: #ff069f;
  outline: none;
}

.form__btn {
  width: 80px;
  height: 25px;
  color: #3200ba;
  cursor: pointer;
  background-color: #fff;
  border-style: none;
  border-radius: 50px;
  font-family: Lato, sans-serif;
  font-weight: 400;
}

.form__btn:hover {
  color: #f0f8ff;
  background-color: #ff00f7;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin-bottom: 20px;
  padding: 20px 0;
  display: flex;
}

img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  display: block;
}

.photo-card {
  width: 350px;
  background-color: #add8e6;
  border: 1px solid #000;
  border-radius: 10px;
  display: inline-block;
}

.photo {
  min-width: 350px;
  height: 250px;
}

.info-item {
  color: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Lato, sans-serif;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.load-more {
  width: 130px;
  height: 40px;
  color: #fff;
  cursor: pointer;
  background: none;
  border-radius: 5px;
  outline: none;
  justify-content: center;
  padding: 10px 25px;
  font-family: Lato, sans-serif;
  font-weight: 500;
  transition: all .3s;
  display: inline-block;
  position: relative;
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, .5), 7px 7px 20px rgba(0, 0, 0, .1), 4px 4px 5px rgba(0, 0, 0, .1);
}

.load {
  width: 130px;
  height: 40px;
  background: linear-gradient(0deg, #0059ff 0%, #0283fb 100%);
  border: none;
  margin: 40px auto;
  padding: 0;
  line-height: 42px;
  display: block;
}

.load:hover {
  color: #1109f0;
  box-shadow: none;
  background: none;
}

.load:before, .load:after {
  content: "";
  height: 2px;
  width: 0;
  background: #0d09f0;
  transition: all .4s;
  position: absolute;
  top: 0;
  right: 0;
  box-shadow: -1px -1px 5px #fff, 7px 7px 20px rgba(0, 0, 0, .2), 4px 4px 5px rgba(0, 0, 0, .133);
}

.load:after {
  right: inherit;
  top: inherit;
  bottom: 0;
  left: 0;
}

.load:hover:before, .load:hover:after {
  width: 100%;
  transition: all .8s;
}

